import {
  CREATE_TUTORIAL,
  RETRIEVE_TUTORIALS,
  UPDATE_TUTORIAL,
  DELETE_TUTORIAL,
  TOGGLE_FORM,
  CLOSE_FORM,
  OPEN_FORM,
  EDIT_TASK,
  FILTER_TABLE,
  SORT_TUTORIAL
} from "./types";

import TutorialDataService from "./../../services/tutorial.service";

export const createTutorial = (name, status) => async (dispatch) => {
  try {
    const res = await TutorialDataService.create({ name, status });
    
    dispatch({
      type: CREATE_TUTORIAL,
      payload: res.data,
    });

    return Promise.resolve(res.data);
  } catch (err) {
    return Promise.reject(err);
  }
};

export const retrieveTutorials = () => async (dispatch) => {
  try {
    const res = await TutorialDataService.getAll();

    dispatch({
      type: RETRIEVE_TUTORIALS,
      payload: res.data,
    });
  } catch (err) {
    console.log(err);
  }
};

export const findTutorialsByTitle = (title) => async (dispatch) => {
  try {
    const res = await TutorialDataService.findByTitle(title);

    dispatch({
      type: RETRIEVE_TUTORIALS,
      payload: res.data,
    });
  } catch (err) {
    console.log(err);
  }
};

export const updateTutorial = (id, data) => async (dispatch) => {
  try {
    const res = await TutorialDataService.update(id, data);

    dispatch({
      type: UPDATE_TUTORIAL,
      payload: data,
    });

    return Promise.resolve(res.data);
  } catch (err) {
    return Promise.reject(err);
  }
};

export const deleteTutorial = (id) => async (dispatch) => {
  try {
    await TutorialDataService.delete(id);

    dispatch({
      type: DELETE_TUTORIAL,
      payload: { id },
    });
  } catch (err) {
    console.log(err);
  }
};

export const filterTutorial = (filter) => (dispatch) => {
	dispatch({
		type: FILTER_TABLE,
		filter
	});
};

export const sortTutorial = (sort) => (dispatch) => {
	dispatch({
		type: SORT_TUTORIAL,
		sort
	});
};

export const editTutorial = (tutorial) => (dispatch) => {
	dispatch({
		type: EDIT_TASK,
		payload: tutorial
	});
};

export const toggleForm = () => (dispatch) => {
  dispatch({
    type: TOGGLE_FORM,
  });
};

export const openForm = () => (dispatch) => {
  dispatch({
    type: OPEN_FORM,
  });
};

export const closeForm = () => (dispatch) => {
  dispatch({
    type: CLOSE_FORM,
  });
};

