import React, { Component } from "react";
import TutorialItem from "./tutorial-Item";
import { connect } from "react-redux";
import {
  retrieveTutorials,
  filterTutorial,
} from "../../actions/tutorial-actions/tutorials";

class TutorialList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      filterName: "",
      filterStatus: -1,
    };
  }

  componentDidMount = () => {
    this.props.retrieveTutorials();
  };

  onChange = (event) => {
    var target = event.target;
    var name = target.name;
    var value = target.value;

    var filter = {
      name: name === "filterName" ? value : this.state.filterName,
      status: name === "filterStatus" ? value : this.state.filterStatus,
    };

    this.props.filterTutorial(filter);

    this.setState({
      [name]: value,
    });
  };

  render() {
    var { tutorials, filterTable, sortTutorial } = this.props;
    var { filterName, filterStatus } = this.state;

    if (tutorials) {
      if (filterTable.name) {
        tutorials = tutorials.filter((tutorial) => {
          return tutorial.name.toLowerCase().indexOf(filterTable.name) !== -1;
        });
      }

      tutorials = tutorials.filter((tutorial) => {
        if (filterTable.status === -1) {
          return tutorials;
        } else {
          return tutorial.status === (filterTable.status === 1 ? true : false);
        }
      });

      if (sortTutorial.by === "name") {
        tutorials.sort((a, b) => {
          if (a.name > b.name) return sortTutorial.value;
          else if (a.name < b.name) return -sortTutorial.value;
          else return 0;
        });
      } else {
        tutorials.sort((a, b) => {
          if (a.status > b.status) return -sortTutorial.value;
          else if (a.status < b.status) return sortTutorial.value;
          else return 0;
        });
      }

      var elmTutorials = tutorials.map((tutorial, index) => {
        return (
          <TutorialItem key={tutorial.id} index={index} tutorial={tutorial} />
        );
      });
    }

    return (
      <table className="table table-bordered table-hover mt-15">
        <thead>
          <tr>
            <th className="text-center">STT</th>
            <th className="text-center">Tn</th>
            <th className="text-center">Trng Thi</th>
            <th className="text-center">Hnh ng</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td></td>
            <td>
              <input
                type="text"
                className="form-control"
                name="filterName"
                value={filterName}
                onChange={this.onChange}
              />
            </td>
            <td>
              <select
                className="form-control"
                name="filterStatus"
                value={filterStatus}
                onChange={this.onChange}
              >
                <option value="-1">Tt C</option>
                <option value="0">n</option>
                <option value="1">Kch Hot</option>
              </select>
            </td>
            <td></td>
          </tr>

          {elmTutorials}
        </tbody>
      </table>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    tutorials: state.tutorials,
    filterTable: state.filterTable,
    sortTutorial: state.sortTutorial,
  };
};

export default connect(mapStateToProps, { retrieveTutorials, filterTutorial })(
  TutorialList
);
