import {
  TOGGLE_FORM,
  CLOSE_FORM,
  OPEN_FORM,
} from "./../../actions/tutorial-actions/types";

var initialState = false;

var isDisplayFormReducer = (state = initialState, action) => {
  switch (action.type) {
    case TOGGLE_FORM:
      return !state;
    case OPEN_FORM:
      return true;
    case CLOSE_FORM:
      return false;
    default:
      return state;
  }
};

export default isDisplayFormReducer;
