import React, { Component } from "react";
import { connect } from "react-redux";
import {
  createTutorial,
  closeForm,
  updateTutorial,
} from "../../actions/tutorial-actions/tutorials";

import Input from "react-validation/build/input";
import Form from "react-validation/build/form";
import CheckButton from "react-validation/build/button";

const required = (value) => {
  if (!value) {
    return (
      <div className="alert alert-danger" role="alert">
        This field is required!
      </div>
    );
  }
};

class TutorialForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      id: "",
      name: "",
      status: false,
    };
  }

  componentDidMount() {
    if (this.props.itemEditing) {
      this.setState({
        id: this.props.itemEditing.id,
        name: this.props.itemEditing.name,
        status: this.props.itemEditing.status,
      });
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps && nextProps.itemEditing) {
      this.setState({
        id: nextProps.itemEditing.id,
        name: nextProps.itemEditing.name,
        status: nextProps.itemEditing.status,
      });
    } else if (!nextProps.itemEditing) {
      this.setState({
        id: "",
        name: "",
        status: false,
      });
    }
  }

  onChange = (event) => {
    var target = event.target;

    var name = target.name;
    var value = target.value;

    if (name === "status") {
      value = target.value === "true" ? true : false;
    }

    this.setState({
      [name]: value,
    });
  };

  onSubmit = (event) => {
    event.preventDefault();

    const { id, name, status } = this.state;

    var data = {
      id,
      name,
      status,
    };

    this.form.validateAll();

    if (this.checkBtn.context._errors.length === 0) {
      if (!this.state.id) {
        this.props.createTutorial(name, status);
      } else {
        this.props.updateTutorial(id, data);
      }

      this.onClear();
      this.onCloseForm();
    } 
  
  };

  onCloseForm = () => {
    this.props.closeForm();
  };

  onClear = () => {
    this.setState({
      id: "",
      name: "",
      status: false
    });
  };

  render() {
    if (!this.props.isDisplayForm) return "";

    var { id, name, status } = this.state;

    return (
      <div className="panel panel-warning">
        <div className="panel-heading">
          <h3 className="panel-title">
            {id !== "" ? "Cp Nht Cng Vic" : "Thm Cng Vic"}
            <span
              className="bi bi-x text-right"
              onClick={this.onCloseForm}
            ></span>
          </h3>
        </div>

        <div className="panel-body">
          <Form
            onSubmit={this.onSubmit}
            ref={(c) => {
              this.form = c;
            }}
          >
            <div className="form-group">
              <label>Tn :</label>
              <Input
                type="text"
                className="form-control"
                name="name"
                value={name}
                onChange={this.onChange}
                validations={[required]}
              />
            </div>
            <label>Trng Thi :</label>
            <select
              className="form-control"
              name="status"
              value={status}
              onChange={this.onChange}
            >
              <option value={true}>Kch Hot</option>
              <option value={false}>n</option>
            </select>
            <br />
            <div className="text-center">
              <button
                type="submit"
                className="btn btn-warning"
              >
                Lu Li
              </button>
              &nbsp;
              <button
                type="button"
                className="btn btn-danger"
                onClick={this.onClear}
              >
                Xo Trng
              </button>
            </div>

            <CheckButton
              style={{ display: "none" }}
              ref={(c) => {
                this.checkBtn = c;
              }}
            />
          </Form>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isDisplayForm: state.isDisplayForm,
    itemEditing: state.itemEditing,
  };
};

// const mapDispatchToProps = (dispatch, props) => {
//     return{
//         onSaveTask : (task) => {
//             dispatch(actions.saveTask(task));
//         },
//         onCloseForm: () => {
//             dispatch(actions.closeForm());
//         }
//     }
export default connect(mapStateToProps, {
  createTutorial,
  closeForm,
  updateTutorial,
})(TutorialForm);
