import React, { Component } from "react";
import { connect } from "react-redux";
import { sortTutorial } from "../../actions/tutorial-actions/tutorials";

class TutorialSort extends Component {
  onClick = (sortBy, sortValue) => {
    this.props.sortTutorial({
      by: sortBy,
      value: sortValue,
    });
  };

  render() {

    return (
      <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6">
        <div className="dropdown">
          <button
            className="btn btn-primary dropdown-toggle"
            type="button"
            id="dropdownMenu1"
            data-toggle="dropdown"
          >
            Sp Xp
          </button>

          <ul className="dropdown-menu" aria-labelledby="dropdownMenu1">
            <li onClick={() => this.onClick("name", 1)}>
              <button
                type="button"
                className={
                  this.props.sortTutorial.by === "name" && this.props.sortTutorial.value === 1 ? "sort_selected" : ""
                }
              >
                <span className="fa fa-sort-alpha-asc pr-5">Tn A-Z</span>
              </button>
            </li>
            <li onClick={() => this.onClick("name", -1)}>
              <button
                button="button"
                className={
                  this.props.sortTutorial.by === "name" && this.props.sortTutorial.value === -1 ? "sort_selected" : ""
                }
              >
                <span className="fa fa-sort-alpha-desc pr-5">Tn Z-A</span>
              </button>
            </li>
            <li role="separator" className="divider"></li>
            <li onClick={() => this.onClick("status", 1)}>
              <button
                button="button"
                className={
                  this.props.sortTutorial.by === "status" && this.props.sortTutorial.value === 1
                    ? "sort_selected"
                    : ""
                }
              >
                Trng Thi Kch Hot
              </button>
            </li>
            <li onClick={() => this.onClick("status", -1)}>
              <button
                button="button"
                className={
                  this.props.sortTutorial.by === "status" && this.props.sortTutorial.value === -1
                    ? "sort_selected"
                    : ""
                }
              >
                Trng Thi n
              </button>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    sortTutorial: state.sortTutorial,
  };
};

export default connect(mapStateToProps, { sortTutorial })(TutorialSort);
