import itemEditing from "./tutorial-reducers/itemEditing";
import filterTable from "./tutorial-reducers/filterTable";
import sortTutorial from "./tutorial-reducers/sortTutorial";

export default combineReducers({
  auth,
  message,
  tutorials,
  isDisplayForm,
  itemEditing,
  filterTable,
  sortTutorial
});
