import {
	EDIT_TASK
} from "./../../actions/tutorial-actions/types";

var initialState = {
	id: '',
    name: '',
    status: false
};

var myReducer = (state = initialState, action ) => {

	const { type, payload } = action;
	
	switch(type){
		case EDIT_TASK:
			return payload;
			
		default: return state;
	}
};

export default myReducer;