import React, { Component } from "react";
import TutorialList from "./tutorial-list";
import { connect } from "react-redux";
import { toggleForm } from "../../actions/tutorial-actions/tutorials";

import TutorialForm from "./tutorial-form";
import TutorialControl from "./tutorial-control";

class Tutorial extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showModeratorBoard: false,
      showAdminBoard: false,
    };
  }

  componentDidMount() {
    const user = this.props.user;

    if (user) {
      this.setState({
        showModeratorBoard: user.roles.includes("ROLE_MODERATOR"),
        showAdminBoard: user.roles.includes("ROLE_ADMIN"),
      });
    }
  }

  onToggleForm = () => {
    this.props.toggleForm();
  };

  render() {
    var { isDisplayForm } = this.props;

    const { showModeratorBoard, showAdminBoard } = this.state;

    return (
      <div className="container">
        <div className="text-center">
          <h1>Qun L Cng Vic</h1>
          <hr />
        </div>

        <div className="row">
          <div
            className={
              isDisplayForm ? "col-xs-4 col-sm-4 col-md-4 col-lg-4" : ""
            }
          >
            <TutorialForm />
          </div>

          <div
            className={
              isDisplayForm
                ? "col-xs-8 col-sm-8 col-md-8 col-lg-8"
                : "col-xs-12 col-sm-12 col-md-12 col-lg-12"
            }
          >
            <button
              type="button"
              className="btn btn-primary"
              onClick={this.onToggleForm}
              disabled={!(showAdminBoard || showModeratorBoard)}
            >
              <span className="fa fa-plus"></span>Thm Cng Vic
            </button>

            <TutorialControl />

            <div className="row mt-15">
              <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                <TutorialList></TutorialList>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  const { user } = state.auth;
  
  return {
    isDisplayForm: state.isDisplayForm,
    user,
  };
};

export default connect(mapStateToProps, { toggleForm })(Tutorial);
