import React, { Component } from "react";
import { connect } from "react-redux";
import { findTutorialsByTitle, retrieveTutorials } from "../../actions/tutorial-actions/tutorials";

class TutorialSearch extends Component {
  constructor(props) {
    super(props);

    this.state = {
      keyword: "",
    };
  }

  onChange = (event) => {
    var target = event.target;
    var name = target.name;
    var value = target.value;

    this.setState({
      [name]: value,
    });
  };

  clearKeyword = () => {
    this.setState({
      keyword: "",
    });

    this.props.retrieveTutorials();
  };

  findByTitle = () => {
    this.props.findTutorialsByTitle(this.state.keyword);
  };

  render() {
    var { keyword } = this.state;

    return (
      <div className="col-xs-6 col-sm-6 col-md-6 col-lg-6">
        <div className="input-group">
          <input
            type="text"
            className="form-control"
            name="keyword"
            placeholder="Nhp t kha..."
            value={keyword}
            onChange={this.onChange}
          />

          <span
            className="bi bi-x text-right mt-10 mr-5"
            onClick={this.clearKeyword}
          ></span>

          <span className="input-group-btn">
            <button
              className="btn btn-primary"
              type="button"
              onClick={this.findByTitle}
            >
              <span className="fa fa-search mr-5"></span>Tm
            </button>
          </span>
        </div>
      </div>
    );
  }
}

export default connect(null, { findTutorialsByTitle, retrieveTutorials })(TutorialSearch);
