import React, { Component } from "react";
import {connect} from 'react-redux';
import { updateTutorial, deleteTutorial, openForm, editTutorial } from "../../actions/tutorial-actions/tutorials";

class TutorialItem extends Component {

  constructor(props) {
    super(props);

    this.state = {
      showModeratorBoard: false,
      showAdminBoard: false,
    };

  }

  componentDidMount() {
    const user = this.props.user;

    if (user) {
      this.setState({
        showModeratorBoard: user.roles.includes("ROLE_MODERATOR"),
        showAdminBoard: user.roles.includes("ROLE_ADMIN"),
      });
    }
  }

  onEditTask = () => {
    this.props.openForm();
    this.props.editTutorial(this.props.tutorial);
}

  onDeleteTutorial = () => {
    this.props.deleteTutorial(this.props.tutorial.id);
  }

  findIndex = (tutorials, id) => {
    var result = -1;
  
    tutorials.forEach((tutorial, index) => {
        if(tutorial.id === id){
            result = index;
        }
    });
  
    return result;
  }

  onUpdateStatus = () => {
    
    var data = {
      id : '',
      name: '',
      status: false,
    };

    var { tutorials } = this.props;

    var index = this.findIndex(tutorials, this.props.tutorial.id);

    tutorials[index].status = !tutorials[index].status;

    data = tutorials[index];

    this.props.updateTutorial(this.props.tutorial.id, data);
  }

  render() {
    var { tutorial, index } = this.props;

    const {showModeratorBoard, showAdminBoard } = this.state;

    return (
      <tr>
        <td>{index + 1}</td>
        <td>{tutorial.name}</td>

        <td className="text-center">
          <button
            className = { 
              tutorial.status === true
                ? "btn btn-danger"
                : "btn btn-primary"
            }
            onClick={this.onUpdateStatus} disabled={!(showAdminBoard || showModeratorBoard)}
          >
            {tutorial.status === true ? "Kch Hot" : "n"}
          </button>
        
        </td>

        <td className="text-center">
        
          <button
            type="button"
            className="btn btn-warning"
            onClick={this.onEditTask}  disabled={!(showAdminBoard || showModeratorBoard)}
          >
            <span className="fa fa-pencil mr-5"></span>Sa
          </button>
          &nbsp;
          <button
            type="button"
            className="btn btn-danger"
            onClick={this.onDeleteTutorial} disabled={!(showAdminBoard || showModeratorBoard)}
          >
            <span className="fa fa-trash mr-5"></span>Xa
          </button>
        </td>
      </tr>
    );
  }
}

function mapStateToProps(state) {
  const { user } = state.auth;
  return {
    tutorials: state.tutorials,
    user
  };
}

export default connect(mapStateToProps, {updateTutorial, deleteTutorial, openForm, editTutorial})(TutorialItem);

